@import url(http://fonts.googleapis.com/earlyaccess/notosanskr.css);
body {font-family: 'Noto Sans KR', sans-serif;}

$primary-color : #de9d22;
$blue-color : #0d52a7;
$green-color : #208b73;
$red-color : #d1452e;
$gray-color : #dbdbdb;
$deep-gray-color:#444444;

html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
    margin: 0;
    padding: 0;
    border: 0;
    font-size: 100%;
    font-style: initial;
    vertical-align: baseline;
    font-family: 'Noto Sans KR', sans-serif;
    letter-spacing: -0.2px;
}
ul, li {list-style: none;}
a {text-decoration: none;color: initial;}
button {font-family: 'Noto Sans KR', sans-serif;outline: none;background: initial;cursor: pointer;}
input {outline: none;}
img {vertical-align:middle;}

.mt55 {margin-top:55px !important;;}
.mb35 {margin-bottom:35px !important;;}
.mb30 {margin-bottom:30px !important;}



// 뱃지 스타일
.badge {display:inline-block;padding:4px 15px;font-size:14px;font-weight:700;border-radius:14px;margin-right:5px;}
.badge.best {color:#fe8144;border:1px solid #fe8144;}
.badge.new {color:#269d7d;border:1px solid #269d7d;}
.badge.hot {color:#d1452e;border:1px solid #d1452e;}
.badge.event {color:#1b4c88;border:1px solid #1b4c88;}

// 버튼 사이즈
.lg_btn {display:inline-block;padding: 10px 50px;border:none;box-sizing: border-box;-webkit-box-sizing: border-box;-moz-box-sizing: border-box;}
.md_btn {display:inline-block;padding:10px 25px;border:none;box-sizing: border-box;-webkit-box-sizing: border-box;-moz-box-sizing: border-box;}
.sm_btn {display:inline-block;padding: 5px 10px;border:none;box-sizing: border-box;-webkit-box-sizing: border-box;-moz-box-sizing: border-box;}
.xsm_btn {display:inline-block;padding: 10px 20px;border:none;box-sizing: border-box;-webkit-box-sizing: border-box;-moz-box-sizing: border-box;}

// 라운드 버튼
.round5 {border-radius:5px;}
.round15 {border-radius: 15px;}
.round30 {border-radius: 30px;}

// 버튼 스타일
.line_red {color:#b94a38;border:1px solid #b94a38;}
.line_gray {color:#bababa;border:1px solid #bababa;}
.line_text_gray {color:#666666;border:1px solid #eaeaea;}
.line_white {color:#fff;border:1px solid #fff;}
.line_blue {color:#0d52a7;border:1px solid #0d52a7;}
.bg_red {background:#b94a38;color:#fff;border:1px solid #b94a38;}
.bg_gray {background:#444444;color:#fff;border:1px solid #444444;}
.bg_light_gray {background:#bbbbbb;color:#fff;border:1px solid #bbbbbb;}
.bg_blue {background:#1b4c88;color:#fff;border:1px solid #1b4c88;}
.bg_white {background:#fff;border:1px solid #fff;border:1px solid #fff;}

// 평점 
.star {position: relative;padding-left:30px;}
.star::before {content:'';position: absolute;top:0;left:0;background:url('../images/star.png');width:24px;height:23px;}

// 더보기
.showMore {font-size:14px;color:#0d52a7;font-weight:500;}

// input 스타일
input[type="checkbox"] {
    display:none;
    &+label {
        position: relative;
        display:inline-block;
        margin-right:15px;
        padding-left: 25px;
        color:#333333;
        cursor: pointer;
        vertical-align: middle;
        &:after {
            position: absolute;
            top:3px;
            left: 0;
            display: inline-block;
            width: 20px;
            height: 20px;
            content: '';
            background:url("../images/checkbox_off.png") top no-repeat;
        }
    }
    &:checked+label {
        color:#0d52a7;
        &:after {
            background:url("../images/checkbox_on.png") top no-repeat;
        }
    }
}

// slick 슬라이드
.slick-initialized .slick-slide {outline:none;}